body{
  
  background-image: url("2.jpg");
}
.App{
  
  min-height:500px;
  width: 400px;
  margin: 40px auto;
}

#to-do-form input{
  background-color: rgb(55, 241, 95);
  border: 0; 
  color: rgb(233, 224, 224);
  width: 200px;
  height: 50px;
  margin: 20px;
  padding: 0 20px;
  font-size: 18px;
  border-radius: 10px;
}
#to-do-form input::placeholder{
  color: rgb(255, 255, 255);
}
#to-do-form input:focus{
  outline: none;
}
#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color:rgb(236, 221, 89); 
  cursor: pointer;
}
#to-do-form button:focus{
  outline: none;
}
